.globalHeader{
    display: flex;
    width:100vd;
    background-color: #02101f;
    align-items:center;
    padding:10px 5vw;
    position: relative;
}
.headerLogo{
    height: 30px;
}
.globalHeader>nav>a{
    color:#eee;
    margin-left: 2vw;
}
.globalHeader>nav>.active{
    text-decoration:underline;
}
.buttonWrapper{
    position: absolute;
    right: 5vw;
}
.buttonWrapper>button{
    margin-right:2vw;
}
.buttonWrapper>span{
    color:#eee;
    margin-right:2vw;
}